import { Component, OnInit } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { concat, EMPTY, timer } from "rxjs";
import { switchMapTo } from "rxjs/operators";
import { RefService, TeacherService } from 'src/app/core/services';
import { ActionEnum } from 'src/app/shared/components/cm-table-container/models/config-column.model';
import { Teacher } from 'src/app/core/models/teacher.model';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Observable } from 'rxjs';
import { Department } from 'src/app/core/models';
import { DataValue } from 'src/app/shared/components/cm-table-container/models/data-value.model';
import { NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { TeacherModalComponent } from './teacher-modal/teacher-modal.component';

@Component({
    templateUrl: './teachers.component.html',
    styleUrls: ['./teachers.component.scss']
})
export class TeachersComponent implements OnInit {

    public teachersLength: number;

    public form: FormGroup;

    public teachers: any[];

    public departements$: Observable<Department[]>;

    constructor(private fb: FormBuilder,
        private modalService: NgbModal,
        private spinner: NgxSpinnerService,
        private refService: RefService,
        private teacherService: TeacherService) {
    }

    ngOnInit() {
        this.initForm();
        this.spinner.show();
        this.departements$ = this.refService.getDepartements();
        this.teacherService.getTeachers().subscribe((teachers: any[]) => {
            this.teachersLength = teachers.length;
            this.teachers = this.mapTeachers(teachers);
            this.spinner.hide();
        })
    }

    private mapTeachers(teachers): any {
        let data: any[] = [];
        teachers.forEach(teacher => {
            let result = teacher.disponibilities.filter(item => item.motif == "Surveiller");
            if (result && result.length > 0) {
                result.forEach(element => {
                    let hourR: any;
                    let hour = result.reduce((acc, { startHour, endHour }) => acc + endHour - startHour, 0).toString();
                    if (hour.length === 3) {
                        hourR = hour.substring(0, 1) + "." + hour.substring(1, 3);
                    } else if (hour.length === 4) {
                        hourR = hour.substring(0, 2) + "." + hour.substring(2, 4);
                    }
                    data.push({
                        teacherId: teacher.teacherId,
                        fullName: teacher.fullName,
                        email: teacher.email,
                        phone: teacher.phone,
                        up: teacher.up,
                        departement: teacher.departement ? teacher.departement.label : null,
                        nbr: result.length,
                        nbrHour: hourR
                    })
                });
            } else {
                data.push({
                    teacherId: teacher.teacherId,
                    fullName: teacher.fullName,
                    email: teacher.email,
                    phone: teacher.phone,
                    up: teacher.up,
                    departement: teacher.departement ? teacher.departement.label : null,
                    nbr: 0,
                    nbrHour: 0
                })
            }
        })
        return data;
    }

    public onSearch() {
        if (this.form.valid) {
            this.spinner.show();
            this.teacherService.searchTeachers(this.form.value).subscribe(teachers => {
                this.teachersLength = teachers.length;
                this.spinner.hide();
            });
        }
    }

    public openModal() {
        const modal: NgbModalRef = this.modalService.open(TeacherModalComponent,
            {
                size: 'xl',
                ariaLabelledBy: 'modal-basic-title',
                keyboard: false,
                backdrop: 'static',
                centered: true
            });
        modal.componentInstance.triggerSave.subscribe((dataValue: DataValue) => {
            this.spinner.show();
            if (dataValue.action === ActionEnum.CREATE) {
                concat(
                    this.teacherService.addTeacher(dataValue.value).pipe(switchMapTo(EMPTY)),
                    timer(1000).pipe(switchMapTo(EMPTY)),
                    this.teacherService.getTeachers()
                ).subscribe((teachers: Teacher[]) => {
                    this.spinner.hide();
                    this.teachersLength = teachers.length;
                    modal.componentInstance.setIsSaved({ isSaved: true });
                }, error => {
                    this.spinner.hide();
                    if (error.error.code === 701) {
                        modal.componentInstance.setIsSaved({ isSaved: false, code: error.error.code });
                    }
                })
            }
        });
    }

    public reset() {
        this.form.reset();
        this.spinner.show();
        this.teacherService.getTeachers().subscribe(teachers => {
            this.teachersLength = teachers.length;
            this.spinner.hide();
        });
    }

    private initForm(): void {
        this.form = this.fb.group({
            teacherId: [null],
            cin: [null],
            departement: [null],
            classs: [null]
        })
    }


    public initTeachersColomns(): any[] {
        return [
            {
                header: "N",
                field: "teacherId"
            },
            {
                header: "Enseignant"
            },
            {
                header: "Tlphones",
                field: "phone"
            },
            {
                header: "E-mail",
                field: "email"
            },
            {
                header: "Dpartement",
                field: "departement"
            },
            {
                header: "UP",
                field: "up"
            },
            {
                header: "Nbr surveillances",
                field: "nbr"
            },
            {
                header: "Nbr-H surveiller",
                field: "nbrHour"
            }
        ]
    }
}